.navigation {
    @include flex-between;
    margin-top: 1.8rem;

    ul {
        @include flex-end;
        margin: 30px 40px 0px 0px;
        align-items: flex-end;

        a {
            text-decoration: none;
            padding: 5px;
            width: auto;

            li {
                @include flex-end;
                font-size: 24px;
                font-family: " montserrat";
                width: 90px;
                font-weight: 500;
                list-style: none;
                color: $color-1;
                transition: 0.2s;
                cursor: pointer;

                &:hover {
                    color: $color-1;
                    width: 90px;
                }
            }

            .menu-home {
                width: 136px;
                text-align: right;
                margin-right: 10px;
            }

            .menu-about {
                width: 116px;
            }

            .img {
                @include flex-start;
                margin-top: 30px;
            }
        }

        //*barre en dessous de la nav
        .nav-active {
            position: relative;

            &::after {
                content: "";
                height: 2px;
                width: min-content;
                background: $color-1;
                position: absolute;
                border-radius: 10px;
                bottom: 0px;
                left: 50%;
                width: 0;
                transform: translateX(-50%);
                -webkit-animation: anim 0.3s ease forwards;
                animation: anim 0.6s ease forwards;

                @-webkit-keyframes anim {
                    to {
                        width: 90%;
                    }
                }

                @keyframes anim {
                    to {
                        width: 100%;
                    }
                }
            }
        }
    }
}